.wrap_border{
    position:absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
}
.wrap{
    position:absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    width:295px;
    padding:20px 0 10px;
    background:#fff;
}
.red{
    color:#FF0000;
}
.green{
    color:#4ED1C0;
}
.title{
    color:#4ED1C0;
    font-size:17px;
    text-align: center;
    margin-bottom:5px;
}
.content{
    color:#B9B9B9;
    width:85%;
    height:110px;
    font-size:10px;
    margin:0 auto;
    overflow: scroll;
    scrollbar-width: none;
}
.content2{
    color:#B9B9B9;
    width:85%;
    font-size:10px;
    line-height: 20px;
    margin:50px auto;
}
.content::-webkit-scrollbar {
    display: none;
}
.bottom_wrap{
    width:85%;
    margin:10px auto 0;
}
.button1{
    background: linear-gradient(90deg, #93FFE4 0%, #6BFFD0 24%, #30C3D0 81%);
    box-shadow: 0px 1px 0px 0px rgba(58,89,65,0.2);
    border-radius: 4px;
    font-size: 14px;
    font-family: HYQiHei;
    font-weight: normal;
    color: #FFFFFF;
    line-height: 35px;
    height:35px;
    text-align: center;
}
.button2{
    margin-top:10px;
    color: #B9B9B9;
    text-align: center;
    font-size: 12px;
}
.bottom_wrap2{
    width:85%;
    margin:10px auto 20px;
    display:flex;
    justify-content: space-around;
}
.button_s1{
    width:40%;
    line-height: 35px;
    height:35px;
    font-size:14px;
    text-align: center;
    color:#B9B9B9;
    border: 1px solid #ECECEC;
    border-radius: 4px;
    box-sizing: border-box;
}
.button_s2{
    width:40%;
    line-height: 35px;
    height:35px;
    color: #FFFFFF;
    text-align: center;
    font-size:14px;
    background: linear-gradient(90deg, #93FFE4 0%, #6BFFD0 24%, #30C3D0 81%);
    border-radius: 4px;
}

@media (orientation: portrait) {
    .wrap{
        width:90vw;
    }
    .content{
        height:auto;
        overflow: none;
    }
}
@media (min-width:800px){
    .content{
        height:auto;
        overflow: none;
    }
    .wrap{
        width:50vw;
    }
}